import React from 'react'

export default function AboutUs() {
  return (
    <section className='about-us'>
      <div className='anchor-link' id='about-us'></div>
      <div className='container'>
        <h2 className='text-center'>About us</h2>
        <p>Welcome to SW Motors, your trusted local motor trader dedicated to providing quality vehicles at affordable prices. As a small business, we take pride in offering a personalized car-buying experience that puts your needs first.</p>
        <p>With years of experience working on cars, we carefully select each vehicle in our inventory to ensure reliability, value, and satisfaction.</p>
        <p style={{ marginBottom: 0 }}>At SW Motors, customer service is at the heart of everything we do. We believe in transparency, honesty, and integrity, ensuring that you drive away with confidence in your purchase. We're here to assist you every step of the way.</p>
      </div>
    </section>
  )
}
