import { useEffect, useState } from "react";
import Page from "../../components/Page"
import createClient from "../../client"
import imageUrlBuilder from "@sanity/image-url";

const builder = imageUrlBuilder(createClient);

function urlFor(source) {
    return builder.image(source);
}

export default function Motors() {

    const [motors, setMotors] = useState([]);

    useEffect(() => {
        createClient
            .fetch(
                `*[_type == "motor"]
                {
                manufacturer, 
                    model, 
                    color, 
                    year,
                    price,
                    image,
                    body,
                    miles,
                    fuel
                }`
            )
            .then((data) => setMotors(data))
            .catch(console.error);
    }, []);

    return (
        <Page>
            <div className="motors container">
                <h1>Current Motors</h1>
                <div className="motors-grid">
                    {
                        motors.map((motor, index) =>
                            <div key={index} className="motor">
                                <div className='img-container'>
                                    <img src={urlFor(motor.image).url()} alt="" />
                                </div>
                                <h3>{motor.manufacturer} {motor.model} {motor.year}</h3>
                                <p>{`${motor.miles} miles - ${motor.color} - ${motor.fuel}`}</p>
                                <p>{motor.price}</p>
                            </div>
                        )
                    }
                </div>
            </div>
        </Page>
    )
}