import React, { useState, useEffect } from 'react'
import { FaWhatsapp } from "react-icons/fa";
// import FbIcon from '../../assets/imgs/fb-icon.svg'
// import InstagramIcon from '../../assets/imgs/instagram-icon.svg'
// import { MdEmail } from "react-icons/md";
import { Link } from 'react-router';

export default function Header() {
  const [isOpen = false, setIsOpen] = useState(0),
    [scrollDown, setScrollDown] = useState(false);

  useEffect(() => {
    isOpen && (document.body.style.overflow = 'hidden');
    !isOpen && (document.body.style.overflow = '');
  }, [isOpen]);

  useEffect(() => {
    if (typeof window !== "undefined") {
      const handler = () => {
        if (
          window.innerWidth > 1100 &&
          window.pageYOffset > 200
        ) {
          return true
        }
        else {
          return false
        }
      }
      window.addEventListener("scroll", () =>
        setScrollDown(handler)
      );
    }
  }, [scrollDown]);

  return (
    <div className='navigation'>

      <nav className={isOpen ? 'show-nav' : ''}>
        <ul>
          <li><Link to='/' tabIndex='-1' onClick={() => setIsOpen(!isOpen)}>Home</Link></li>
          <li><Link to='/motors' tabIndex='-1' onClick={() => setIsOpen(!isOpen)}>Motors</Link></li>
        </ul>
        {/* <a className='facebook' href='#' target='_blank' rel='noreferrer' tabIndex='-1'>
          <img src={FbIcon} alt='Facebook logo' />
        </a>
        <a className='instagram' href='#' target='_blank' rel='noreferrer' tabIndex='-1'>
          <img src={InstagramIcon} alt='Instagram logo' />
        </a> */}
      </nav>

      <header className={scrollDown ? 'container minimised' : 'container'}>
        <Link to='/'><h1>SW Motors</h1></Link>
        <div className='header-links'>
          <ul>
            <li><Link to='/'>Home</Link></li>
            <li><Link to='/motors'>Motors</Link></li>
            <li><a href='https://wa.me/447770385535' target='_blank' rel='noreferrer'><FaWhatsapp /></a></li>
          </ul>
        </div>
      </header>

      <div className={isOpen ? 'burger-menu cross' : 'burger-menu'} onClick={() => setIsOpen(!isOpen)}>
        <div className='burger-menu-container'>
          <span></span>
          <span></span>
          <span></span>
        </div>
      </div>
    </div>

  )
}
