import React from 'react'
import LogoNoBg from '../../assets/imgs/logo-removebg.png'
import Backdrop from '../../assets/imgs/backdrop.jpg'
import { Link } from 'react-router'

export default function Hero() {
  return (
    <div className='hero'>
      <img src={Backdrop} className="bg-img" alt='forest' style={{ objectFit: "stretch" }} />
      <div className='hero-text'>
        <img src={LogoNoBg} className="logo-img" alt="logo" />
        <p>Fueling Your Drive, Powering Your Trust!</p>
        <Link to='/motors' className='button'>View our Motors</Link>
      </div>
    </div>
  )
}
