import React from 'react'

export default function ContactUs() {
    return (
        <section className='contact-us'>
            <div className='anchor-link' id='contact'></div>
            <div className='container'>
                <h2 className='text-center'>Contact us</h2>

                <p>If you'd like to arrange a viewing, please send us a Whatsapp message or an email and we will get back to you as soon as we can.</p>

                <ul className='list contact-list'>
                    <li className='whatsapp'>
                        <a href='tel:https://wa.me/447770385535'>07770 385535</a>
                    </li>
                    <li className='email'>
                        <a href='mailto:sales@swmotorsltd.co.uk'>sales@swmotorsltd.co.uk</a>
                    </li>
                    <li className='address'>
                        Unit 13, Home Farm, Rad Lane, Dorking, Surrey, RH5 6RA
                    </li>
                </ul>
            </div>
        </section>
    )
}
